
.photo-view{padding:1px 3rem;clear:both
}
ul.photolist,ul.photolist>li{margin:0;padding:0
}
ul.photolist li{list-style-type:none;transition:transform 0.1s;position:relative
}
ul.photolist button{position:absolute;right:2px;top:2px;z-index:100;display:none
}
ul.photolist li:hover button{display:block
}
ul.photolist li:hover{transform:scale(1.5);outline:4px solid #6AF;z-index:3;box-shadow:0 10px 20px #222
}
ul.photolist li:hover.vertical{transform:scale(2);outline:3px solid #6AF
}



